import {WPX_Element} from '../../internal/element.js';
import {styles} from './include.css.js';

//-------------------------------------------------------------------
//--- Element: WPX_Include
//-------------------------------------------------------------------
export class WPX_Include extends WPX_Element {

    static get observedAttributes() {
        return ['src'];
    }

// #region Attributes

    //-------------------------------------------------------------------
    //--- Attribute: src
    //-------------------------------------------------------------------
    get src() {return this.getAttribute('src') || '';}
    set src(value) {this.setAttribute('src', value);}

// #endregion

    //-------------------------------------------------------------------
    //--- constructor()
    //-------------------------------------------------------------------
    constructor() {
        super();
        this.init([styles]);
    }

    //-------------------------------------------------------------------
    //--- connectedCallback()
    //-------------------------------------------------------------------
    connectedCallback() {
        //this.loadContent();
    }

    //-------------------------------------------------------------------
    //--- loadContent()
    //-------------------------------------------------------------------
    async loadContent() {
        if (!this.src) return;

        try {
            const res = await fetch(this.src);
            if (!res.ok) throw new Error(`Failed to load ${this.src}`);
            const html = await res.text();
            
            this.setHtml(`<slot>${html}</slot>`);

        } catch (err) {
            console.error(err);
        }
    }

    //-------------------------------------------------------------------
    //--- attributeChangedCallback()
    //-------------------------------------------------------------------
    attributeChangedCallback(name, oldValue, newValue) {
        if (name === 'src' && oldValue !== newValue) {
            this.loadContent();
        }
    }
}

customElements.define('wpx-include', WPX_Include);