import {WPX_Element} from '../../internal/element.js';
import {styles} from './popup.css.js';
import {computePosition, offset, flip, shift} from '@floating-ui/dom';

//-------------------------------------------------------------------
//--- Element: WPX_Popup
//-------------------------------------------------------------------
export class WPX_Popup extends WPX_Element {

	static get observedAttributes() {
		return ['open', 'anchor', 'placement'];
	}

// #region Attributes

    //-------------------------------------------------------------------
    //--- Attribute: open
    //-------------------------------------------------------------------
    get open() {return this.hasAttribute('open');}
    set open(value) {
        value ? this.setAttribute('open', '') : this.removeAttribute('open');
    }

    //-------------------------------------------------------------------
    //--- Attribute: anchor
    //-------------------------------------------------------------------
    get anchor() {return this._anchor;}
    set anchor(el) {
        this._anchor = el;
        this.updatePosition();
    }

    //-------------------------------------------------------------------
    //--- Attribute: placement
    //-------------------------------------------------------------------
    get placement() {return this.getAttribute('placement');}
    set placement(value) {this.setAttribute('placement', value);}

// #endregion

    //-------------------------------------------------------------------
    //--- constructor()
    //-------------------------------------------------------------------
    constructor() {
        super();
        this.init([styles]);
        this.open = false;
    }

    //-------------------------------------------------------------------
    //--- connectedCallback()
    //-------------------------------------------------------------------
    connectedCallback() {
        this.setHtml(`<slot></slot>`);
    }

    //-------------------------------------------------------------------
    //--- updatePosition()
    //-------------------------------------------------------------------
    async updatePosition() {
        if (!this.anchor) return;

        const {x, y} = await computePosition(this.anchor, this, {
            placement: this.placement || 'bottom',
            middleware: [offset(1), flip(), shift({padding: 5})]
        });

        Object.assign(this.style, {left: `${x}px`, top: `${y}px`});
    }

    //-------------------------------------------------------------------
    //--- show(), hide(), toggle()
    //-------------------------------------------------------------------
    show() {this.open = true; this.updatePosition();}
    hide() {this.open = false;}
    toggle() {this.open = !this.open;}
}

customElements.define('wpx-popup', WPX_Popup);