import {WPX_Element} from '../../internal/element.js';
import {styles} from './dialog.css.js';

//-------------------------------------------------------------------
//--- Element: WPX_Dialog
//-------------------------------------------------------------------
export class WPX_Dialog extends WPX_Element {

    static get observedAttributes() {
        return ['open'];
    }

// #region Attributes

    //-------------------------------------------------------------------
    //--- Attribute: open
    //-------------------------------------------------------------------
    get open() {return this.hasAttribute('open');}
    set open(value) {
        value ? this.setAttribute('open', '') : this.removeAttribute('open');
        value ? this.dialog.showModal() : this.dialog.close();
    }

// #endregion

    //-------------------------------------------------------------------
    //--- constructor()
    //-------------------------------------------------------------------
    constructor() {
        super();
        this.init([styles]);
    }

    //-------------------------------------------------------------------
    //--- connectedCallback()
    //-------------------------------------------------------------------
    connectedCallback() {
        this.render();
        
        this.dialog = this.shadowRoot.querySelector('dialog');
        this.dialog.addEventListener('close', () => {
            this.hide();
        });
    }

    //-------------------------------------------------------------------
    //--- render()
    //-------------------------------------------------------------------
    render() {
        this.setHtml(`
            <dialog class="dialog">
                <slot name="header"></slot>
                <slot></slot>
                <slot name="footer"></slot>
            </dialog>
        `);
    }

    //-------------------------------------------------------------------
    //--- show()
    //-------------------------------------------------------------------
    show() {
        if (this.open) return;
        this.dispatchEvent(new CustomEvent('wpx-show', {bubbles: true}));
        this.open = true;
    }

    //-------------------------------------------------------------------
    //--- hide()
    //-------------------------------------------------------------------
    hide() {
        if (!this.open) return;
        this.dispatchEvent(new CustomEvent('wpx-hide', {bubbles: true}));
        this.open = false;
    }
}

customElements.define('wpx-dialog', WPX_Dialog);