import {WPX_Element} from '../../internal/element.js';
import {styles} from './spinner.css.js';

//-------------------------------------------------------------------
//--- Element: WPX_Spinner
//-------------------------------------------------------------------
export class WPX_Spinner extends WPX_Element {

    //-------------------------------------------------------------------
    //--- constructor()
    //-------------------------------------------------------------------
    constructor() {
        super();
        this.init([styles]);
    }

    //-------------------------------------------------------------------
    //--- connectedCallback()
    //-------------------------------------------------------------------
    connectedCallback() {
        if (!this.shadowRoot.querySelector('.spinner')) {
            const div = document.createElement('div');
            div.classList.add('spinner');
            this.shadowRoot.appendChild(div);
        }
    }
}

customElements.define('wpx-spinner', WPX_Spinner);