export const styles = /*css*/ `
:host {
    display: inline-flex;
    flex-direction: column;
    gap: var(--wpx-spacing-xs);
}

label {
    position: relative;
    display: flex;
    align-items: center;
    gap: var(--wpx-spacing-xs);
    cursor: pointer;
}

input[type="checkbox"] {
    position: absolute;
    opacity: 0;
    width: 0;
    height: 0;
}

/*------------------------------------*/
/* --- checked ---------------------- */
/*------------------------------------*/
input[type="checkbox"]:checked + .box {
    background: var(--wpx-color-primary-50);
    border-color: var(--wpx-color-primary-50);
}
input[type="checkbox"]:checked + .box wpx-icon {
    opacity: 1;
    transform: scale(1);
}

/*------------------------------------*/
/* --- box -------------------------- */
/*------------------------------------*/
.box {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 2px;
    border: 1px solid var(--wpx-color-neutral-60);
    background-color: var(--wpx-color-neutral-100);
    transition: background-color 0.12s ease
}

.box wpx-icon {
    opacity: 0;
    transform: scale(0.4);
    transition: opacity 0.18s ease, transform 0.18s ease 0.1s;
    pointer-events: none;
    color: var(--wpx-color-neutral-100);
}

/*------------------------------------*/
/* --- disabled --------------------- */
/*------------------------------------*/
:host([disabled]) {
    opacity: 0.6;
    cursor: not-allowed;
}

/*------------------------------------*/
/* --- sizes ------------------------ */
/*------------------------------------*/
:host([size="sm"]) label {
    font-size: var(--wpx-font-size-sm);
}

:host([size="md"]) label {
    font-size: var(--wpx-font-size-md);
}

:host([size="lg"]) label {
    font-size: var(--wpx-font-size-lg);
}
`;