import {WPX_Element} from '../../internal/element.js';
import {styles} from './details.css.js';

//-------------------------------------------------------------------
//--- Element: WPX_Details
//-------------------------------------------------------------------
export class WPX_Details extends WPX_Element {

    static get observedAttributes() {
        return ['open', 'label', 'fill', 'disabled'];
    }

// #region Attributes

    //-------------------------------------------------------------------
    //--- Attribute: open
    //-------------------------------------------------------------------
    get open() {return this.hasAttribute('open');}
    set open(value) {
        value ? this.setAttribute('open', '') : this.removeAttribute('open');
    }

    //-------------------------------------------------------------------
    //--- Attribute: label
    //-------------------------------------------------------------------
    get label() {return this.getAttribute('label') || '';}
    set label(value) {this.setAttribute('label', value);}

    //-------------------------------------------------------------------
    //--- Attribute: fill
    //-------------------------------------------------------------------
    get fill() {return this.getAttribute('fill');}
    set fill(value) {this.setAttribute('fill', value);}

// #endregion

    //-------------------------------------------------------------------
    //--- constructor()
    //-------------------------------------------------------------------
    constructor() {
        super();
        this.init([styles]);
    }

    //-------------------------------------------------------------------
    //--- connectedCallback()
    //-------------------------------------------------------------------
    connectedCallback() {
        if (!this.fill) this.fill = 'outline';
        this.render();

        const header = this.shadowRoot.querySelector('.details--header');
        if (!header) return;

        header.addEventListener('click', () => {
            if (!this.disabled) {
                this.toggle();
            }
        });
    }

    //-------------------------------------------------------------------
    //--- render()
    //-------------------------------------------------------------------
    render() {
        this.setHtml(`
            <div class="details">
                <div class="details--header">
                    <slot name="prefix"></slot>
                    <slot name="label">${this.label}</slot>
                    <slot name="suffix"></slot>
                    <slot name="show-icon"><wpx-icon name="chevron-down"></wpx-icon></slot>
                    <slot name="hide-icon"><wpx-icon name="chevron-up"></wpx-icon></slot>
                </div>
                <div class="details--content">
                    <slot></slot>
                </div>
            </div>
        `);
    }

    //-------------------------------------------------------------------
    //--- toggle()
    //-------------------------------------------------------------------
    toggle() {
        this.open = !this.open;
    }
}

customElements.define('wpx-details', WPX_Details);