import {WPX_Element} from '../../internal/element.js';
import {styles} from './radio.css.js';

//-------------------------------------------------------------------
//--- Element: WPX_Radio
//-------------------------------------------------------------------
export class WPX_Radio extends WPX_Element {

    static get observedAttributes() {
        return ['checked', 'value', 'disabled'];
    }

// #region Attributes

    //-------------------------------------------------------------------
    //--- Attribute: checked
    //-------------------------------------------------------------------
    get checked() {return this.hasAttribute('checked');}
    set checked(value) {
        value ? this.setAttribute('checked', '') : this.removeAttribute('checked');
    }

    //-------------------------------------------------------------------
    //--- Attribute: value
    //-------------------------------------------------------------------
    get value() {return this.getAttribute('value') || '';}
    set value(value) {this.setAttribute('value', value);}

// #endregion

    //-------------------------------------------------------------------
    //--- constructor()
    //-------------------------------------------------------------------
    constructor() {
        super();
        this.init([styles]);
    }

    //-------------------------------------------------------------------
    //--- connectedCallback()
    //-------------------------------------------------------------------
    connectedCallback() {
        this.render();

        this.input = this.shadowRoot.querySelector('input');
        this.input.checked = this.checked;
        this.input.disabled = this.disabled;
        this.input.value = this.value;

        this.input.addEventListener('change', () => this.onChange());
    }

    //-------------------------------------------------------------------
    //--- render()
    //-------------------------------------------------------------------
    render() {
        this.setHtml(`
            <label>
                <input type="radio">
                <span class="circle">
                    <wpx-icon name="circle-fill"></wpx-icon>
                </span>
                <slot></slot>
            </label>
        `);
    }

    //-------------------------------------------------------------------
    //--- onChange()
    //-------------------------------------------------------------------
    onChange() {
        this.checked = this.input.checked;
        this.dispatchEvent(new CustomEvent('wpx-change', {
            detail: {checked: this.checked, value: this.value},
            bubbles: true,
            composed: true
        }));
    }
}

customElements.define('wpx-radio', WPX_Radio);