import {WPX_Element} from '../../internal/element.js';
import {styles} from './breadcrumb.css.js';

//-------------------------------------------------------------------
//--- Element: WPX_Breadcrumb
//-------------------------------------------------------------------
export class WPX_Breadcrumb extends WPX_Element {

    //-------------------------------------------------------------------
    //--- constructor()
    //-------------------------------------------------------------------
    constructor() {
        super();
        this.init([styles]);
    }

    //-------------------------------------------------------------------
    //--- connectedCallback()
    //-------------------------------------------------------------------
    connectedCallback() {
        this.render();
    }

    //-------------------------------------------------------------------
    //--- render()
    //-------------------------------------------------------------------
    render() {
        this.setHtml(`<slot></slot>`);
    }
}

customElements.define('wpx-breadcrumb', WPX_Breadcrumb);