import {WPX_Element} from '../../internal/element.js';
import {styles} from './tab-panel.css.js';

//-------------------------------------------------------------------
//--- Element: WPX_TabPanel
//-------------------------------------------------------------------
export class WPX_TabPanel extends WPX_Element {

    static get observedAttributes() {
        return ['name'];
    }

// #region Attributes

    //-------------------------------------------------------------------
    //--- Attribute: name
    //-------------------------------------------------------------------
    get name() {return this.getAttribute('name');}
    set name(value) {this.setAttribute('name', value);}

// #endregion

    //-------------------------------------------------------------------
    //--- constructor()
    //-------------------------------------------------------------------
    constructor() {
        super();
        this.init([styles]);
    }

    //-------------------------------------------------------------------
    //--- connectedCallback()
    //-------------------------------------------------------------------
    connectedCallback() {
        this.render();
    }

    //-------------------------------------------------------------------
    //--- render()
    //-------------------------------------------------------------------
    render() {
        const panel = document.createElement('div');
        panel.innerHTML = `<slot></slot>`;

        this.shadowRoot.append(panel);
    }
}

customElements.define('wpx-tab-panel', WPX_TabPanel);