import {WPX_Element} from '../../internal/element.js';
import {styles} from './select.css.js';

//-------------------------------------------------------------------
//--- Element: WPX_Select
//-------------------------------------------------------------------
export class WPX_Select extends WPX_Element {

    static get observedAttributes() {
        return ['label', 'hint', 'placeholder', 'size', 'pill', 'value', 'disabled'];
    }

// #region Attributes

    //-------------------------------------------------------------------
    //--- Attribute: label
    //-------------------------------------------------------------------
    get label() {return this.getAttribute('label') || '';}
    set label(value) {this.setAttribute('label', value);}

    //-------------------------------------------------------------------
    //--- Attribute: hint
    //-------------------------------------------------------------------
    get hint() {return this.getAttribute('hint') || '';}
    set hint(value) {this.setAttribute('hint', value);}

    //-------------------------------------------------------------------
    //--- Attribute: placeholder
    //-------------------------------------------------------------------
    get placeholder() {return this.getAttribute('placeholder') || '';}
    set placeholder(value) {this.setAttribute('placeholder', value);}

    //-------------------------------------------------------------------
    //--- Attribute: size
    //-------------------------------------------------------------------
    get size() {return this.getAttribute('size');}
    set size(value) {this.setAttribute('size', value);}

    //-------------------------------------------------------------------
    //--- Attribute: pill
    //-------------------------------------------------------------------
    get pill() {return this.hasAttribute('pill');}
    set pill(value) {
        value ? this.setAttribute('pill', '') : this.removeAttribute('pill');
    }

    //-------------------------------------------------------------------
    //--- Attribute: value
    //-------------------------------------------------------------------
    get value() {
        const input = this.shadowRoot.querySelector('input');
        return input ? input.value : '';
    }
    set value(val) {
        const input = this.shadowRoot.querySelector('input');
        if (input) input.value = val;
    }
    
// #endregion

    //-------------------------------------------------------------------
    //--- constructor()
    //-------------------------------------------------------------------
    constructor() {
        super();
        this.init([styles]);
    }

    //-------------------------------------------------------------------
    //--- connectedCallback()
    //-------------------------------------------------------------------
    connectedCallback() {
        if (!this.size) this.size = 'md';
        this.render();

        const root = this.shadowRoot;
        this.popup = root.querySelector('wpx-popup');
    }

    //-------------------------------------------------------------------
    //--- render()
    //-------------------------------------------------------------------
    render() {
        this.setHtml(`
            <div class="select">
                <slot name="prefix"></slot>
                <input id="input" type="text" placeholder="${this.placeholder}">
                <slot name="suffix"></slot>
            </div>
            <wpx-popup>
                <slot></slot>
            </wpx-popup>
        `);
    }
}

customElements.define('wpx-select', WPX_Select);