import {WPX_Element} from '../../internal/element.js';
import {styles} from './breadcrumb-item.css.js';

//-------------------------------------------------------------------
//--- Element: WPX_BreadcrumbItem
//-------------------------------------------------------------------
export class WPX_BreadcrumbItem extends WPX_Element {

    //-------------------------------------------------------------------
    //--- constructor()
    //-------------------------------------------------------------------
    constructor() {
        super();
        this.init([styles]);
    }

    //-------------------------------------------------------------------
    //--- connectedCallback()
    //-------------------------------------------------------------------
    connectedCallback() {
        this.render();
    }

    //-------------------------------------------------------------------
    //--- render()
    //-------------------------------------------------------------------
    render() {
        const href = this.getAttribute('href');
        const isLast = this === this.parentElement?.lastElementChild;

        this.setHtml(`
            <wpx-button fill="none"><slot></slot></wpx-button>
            ${isLast ? '' : `
                <wpx-icon name="chevron-right"></wpx-icon>
            `}
        `);
    }
}

customElements.define('wpx-breadcrumb-item', WPX_BreadcrumbItem);