import {WPX_Element} from '../../internal/element.js';
import {styles} from './tab.css.js';

//-------------------------------------------------------------------
//--- Element: WPX_Tab
//-------------------------------------------------------------------
export class WPX_Tab extends WPX_Element {

    static get observedAttributes() {
        return ['panel', 'disabled'];
    }

// #region Attributes

    //-------------------------------------------------------------------
    //--- Attribute: panel
    //-------------------------------------------------------------------
    get panel() {return this.getAttribute('panel');}
    set panel(value) {this.setAttribute('panel', value);}

// #endregion

    //-------------------------------------------------------------------
    //--- constructor()
    //-------------------------------------------------------------------
    constructor() {
        super();
        this.init([styles]);
    }

    //-------------------------------------------------------------------
    //--- connectedCallback()
    //-------------------------------------------------------------------
    connectedCallback() {

        // fill Attribut von wpx-tabs auf Element bertragen
        const parentTabs = this.closest('wpx-tabs');
        if (parentTabs) {
            const fill = parentTabs.getAttribute('fill') || 'default';
            this.setAttribute('fill', fill);
        }

        this.render();
    }

    //-------------------------------------------------------------------
    //--- render()
    //-------------------------------------------------------------------
    render() {
        const tabs = document.createElement('div');
        tabs.className = 'tab';
        tabs.innerHTML = `<slot></slot>`;

        this.shadowRoot.append(tabs);
    }
}

customElements.define('wpx-tab', WPX_Tab);