import {WPX_Element} from '../../internal/element.js';
import {styles} from './list.css.js';

//-------------------------------------------------------------------
//--- Element: WPX_List
//-------------------------------------------------------------------
export class WPX_List extends WPX_Element {

    //-------------------------------------------------------------------
    //--- constructor()
    //-------------------------------------------------------------------
    constructor() {
        super();
        this.init([styles]);
    }

    //-------------------------------------------------------------------
    //--- connectedCallback()
    //-------------------------------------------------------------------
    connectedCallback() {
        this.render();
    }

    //-------------------------------------------------------------------
    //--- render()
    //-------------------------------------------------------------------
    render() {
        this.setHtml(`
            <div class="list">
                <slot></slot>
            </div>
        `);
    }
}

customElements.define('wpx-list', WPX_List);