export const styles = /*css*/ `
:host {
    display: inline-block;
    user-select: none;
}

label {
    position: relative;
    display: flex;
    align-items: center;
    gap: var(--wpx-spacing-xs);
    cursor: pointer;
}

input[type="radio"] {
    position: absolute;
    opacity: 0;
    width: 0;
    height: 0;
}

/*------------------------------------*/
/* --- checked ---------------------- */
/*------------------------------------*/
input[type="radio"]:checked + .circle {
    border-color: var(--wpx-color-primary-50);
}
input[type="radio"]:checked + .circle wpx-icon {
    opacity: 1;
}

/*------------------------------------*/
/* --- circle ----------------------- */
/*------------------------------------*/
.circle {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 1.2em;
    height: 1.2em;
    border: 1px solid var(--wpx-color-neutral-60);
    border-radius: 50%;
    background: #fff;
    transition: all 0.16s ease;
}

.circle wpx-icon {
    opacity: 0;
    pointer-events: none;
    color: var(--wpx-color-primary-50);
    font-size: 0.7em;
}

/*------------------------------------*/
/* --- disabled --------------------- */
/*------------------------------------*/
:host([disabled]) {
    opacity: 0.6;
    cursor: not-allowed;
}
`;