import {WPX_Element} from '../../internal/element.js';
import {controlStyles} from '../../styles/control.css.js';
import {styles} from './checkbox.css.js';

//-------------------------------------------------------------------
//--- Element: WPX_Checkbox
//-------------------------------------------------------------------
export class WPX_Checkbox extends WPX_Element {

    static get observedAttributes() {
        return ['checked', 'value', 'size', 'hint', 'disabled'];
    }

// #region Attributes

    //-------------------------------------------------------------------
    //--- Attribute: checked
    //-------------------------------------------------------------------
    get checked() {return this.hasAttribute('checked');}
    set checked(value) {
        value ? this.setAttribute('checked', '') : this.removeAttribute('checked');
    }

    //-------------------------------------------------------------------
    //--- Attribute: value
    //-------------------------------------------------------------------
    get value() {return this.getAttribute('value') || '';}
    set value(value) {this.setAttribute('value', value);}

    //-------------------------------------------------------------------
    //--- Attribute: size
    //-------------------------------------------------------------------
    get size() {return this.getAttribute('size');}
    set size(value) {this.setAttribute('size', value);}

    //-------------------------------------------------------------------
    //--- Attribute: hint
    //-------------------------------------------------------------------
    get hint() {return this.getAttribute('hint') || '';}
    set hint(value) {this.setAttribute('hint', value);}

// #endregion

    //-------------------------------------------------------------------
    //--- constructor()
    //-------------------------------------------------------------------
    constructor() {
        super();
        this.init([styles, controlStyles]);
    }

    //-------------------------------------------------------------------
    //--- connectedCallback()
    //-------------------------------------------------------------------
    connectedCallback() {
        if (!this.size) this.size = 'md';
        this.render();

        this.input = this.shadowRoot.querySelector('input');
        this.input.checked = this.checked;
        this.input.disabled = this.disabled;
        this.input.value = this.value;

        this.input.addEventListener('change', () => this.onChange());
    }

    //-------------------------------------------------------------------
    //--- render()
    //-------------------------------------------------------------------
    render() {
        this.setHtml(`
            <label>
                <input type="checkbox">
                <span class="box">
                    <wpx-icon name="check"></wpx-icon>
                </span>
                <slot class="control-label"></slot>
            </label>
        `);

        if (this.hint) {
            this.setHtml(`
                <div class="control-hint">
                    ${this.hint}
                </div>
            `);
        }
    }

    //-------------------------------------------------------------------
    //--- onChange()
    //-------------------------------------------------------------------
    onChange() {
        if (this.disabled) return;

        this.checked = this.input.checked;
        this.dispatchEvent(new CustomEvent('wpx-change', {
            detail: {checked: this.checked, value: this.value},
            bubbles: true,
            composed: true
        }));
    }
}

customElements.define('wpx-checkbox', WPX_Checkbox);