import {WPX_Element} from '../../internal/element.js';
import {styles} from './divider.css.js';

//-------------------------------------------------------------------
//--- Element: WPX_Divider
//-------------------------------------------------------------------
export class WPX_Divider extends WPX_Element {

    //-------------------------------------------------------------------
    //--- constructor()
    //-------------------------------------------------------------------
    constructor() {
        super();
        this.init([styles]);
    }

    //-------------------------------------------------------------------
    //--- connectedCallback()
    //-------------------------------------------------------------------
    connectedCallback() {
        this.setAttribute('role', 'separator');
    }
}

customElements.define('wpx-divider', WPX_Divider);