import {WPX_Element} from '../../internal/element.js';
import {styles} from './dropdown.css.js';

//-------------------------------------------------------------------
//--- Element: WPX_Dropdown
//-------------------------------------------------------------------
export class WPX_Dropdown extends WPX_Element {

    //-------------------------------------------------------------------
    //--- constructor()
    //-------------------------------------------------------------------
    constructor() {
        super();
        this.init([styles]);
    }

    //-------------------------------------------------------------------
    //--- connectedCallback()
    //-------------------------------------------------------------------
    connectedCallback() {
        this.render();

        const root = this.shadowRoot;
        this.trigger = root.querySelector('slot[name="trigger"]');
        this.popup = root.querySelector('wpx-popup');

        const trigger = this.trigger.assignedElements()[0]; 
        trigger.addEventListener('click', () => {
            this.popup.placement = 'bottom-start';
            this.popup.anchor = trigger;
            this.popup.toggle();
        });

        // Klick auerhalb -> Popup schlieen
        document.addEventListener('click', e => {
            if (!this.contains(e.target)) this.popup.hide();
        });
    }

    //-------------------------------------------------------------------
    //--- render()
    //-------------------------------------------------------------------
    render() {
        this.setHtml(`
            <slot name="trigger"></slot>
            <wpx-popup>
                <slot></slot>
            </wpx-popup>
        `);
    }
}

customElements.define('wpx-dropdown', WPX_Dropdown);